import React, {useContext} from 'react';
import ProjectContext from "../utils/ProjectContext"


export default function ImgMediaCard(props) {
  const {name, repo, deployed} = useContext(ProjectContext)
  console.log(name)
  
 

  
  return (

    
    <div>
      <div className="card bg-light col-md-9 mx-4" style={{maxWidth: "18rem"}}>
        <div className="card-header">{props.name}</div>
        <div className="card-body">
  <h5 className="card-title">{}</h5>
          <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
          <button type="button" className="btn btn-dark mx-auto">Deployed</button>
          <a target="_blank" href={`${props.repo}`} ><button  type="button" className="btn btn-dark mx-auto">Github Repo</button></a>
        </div>
      </div>
    </div>
  );
}