import React from "react";
import { Link, useLocation } from "react-router-dom";
import "../App.css"

function NavTabs() {
  // We'll go into the Hooks API later, for now, we are just using some code
  // from the react-router docs (https://reacttraining.com/react-router/web/api/Hooks/uselocation)
  // This allows the component to check the route any time the user uses a link to navigate.
  const location = useLocation();

  return (
    
    <ul  style={{margin: "0 auto", display: "flex", justifyContent: "center"}} className="nav nav-tabs">
      <li  className="nav-item">
        <Link style={{color: "black", borderRadius: "0px"}} to="/" className={location.pathname === "/" ? "nav-link active" : "nav-link"}>
          Home
        </Link>
      </li>
      <li className="nav-item">
        <Link style={{color: "black"}}
          to="/about"
          className={location.pathname === "/about" ? "nav-link active" : "nav-link"}
        >
          About
        </Link>
      </li>
      <li className="nav-item">
        <Link style={{color: "black"}}
          to="/portfolio"
          className={location.pathname === "/portfolio" ? "nav-link active" : "nav-link"}
        >
          Portfolio
        </Link>
      </li>
      <li style={{borderRadius: "0px"}} className="nav-item">
        <Link style={{color: "black"}}
          to="/contact"
          className={location.pathname === "/contact" ? "nav-link active" : "nav-link"}
        >
          Contact
        </Link>
      </li>
    </ul>
  );
}

export default NavTabs;
